body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(./assets/backgrounds/men.jpg); min-height: 100%; background-size: cover; zoom: 1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1.title {
  color: white
}

.candles {
  background: url(./assets/images/candles.png)
}



.offer {
  background-color: aquamarine;
  color:white;
  border-radius: 10px;
  padding: 10px;
  text-align: center;
}

.offer a {
  margin-top: 10px
}

.big {
  font-size: 1.2em;
 text-decoration: underline;
 color: aquamarine
}

.very-big {
  font-size: 2em;
 text-decoration: none;
 color: aquamarine
}

.sections {
  margin: 10px;
  padding-left: 25%;
  padding-right: 25%;
}

.code {
  background-color: lightgray;
  padding: 10px;
  margin:5px;
}

.padding {
  padding-top: 10px;
  padding-bottom: 10px;
}

.announcement {
  font-size: 2em;
}

.modal-card {
  width: 70% !important;
}

